import styled from 'styled-components';

const Input = styled.input`
  background: transparent;
  border: 0.2rem solid
    ${({ theme, incorrect }) =>
      incorrect ? theme.colors.claret : theme.colors.green};
  border-radius: 1rem;
  margin: 0.5rem 0;
  color: ${({ theme, incorrect }) =>
    incorrect ? theme.colors.claret : theme.colors.lightBlue};
  font-size: 2.5rem;
  text-align: center;
  width: ${({ width }) => width || '80%'};
  font-family: 'Lobster', sans-serif, Arial, Helvetica;
  vertical-align: middle;
  transition: filter 0.3s ease;
  user-select: none;

  ::placeholder {
    color: ${({ theme }) => theme.colors.dark};
    font-size: 2.5rem;
    filter: drop-shadow(
      0rem 0rem 0.5rem
        ${({ theme, incorrect }) =>
          incorrect ? theme.colors.pink : theme.colors.green}
    );
  }

  :focus {
    border: 0.2rem solid
      ${({ theme, incorrect }) =>
        incorrect ? theme.colors.pink : theme.colors.lightBlue};
    outline: none;
    filter: drop-shadow(
      0rem 0rem 0.5rem
        ${({ theme, incorrect }) =>
          incorrect ? theme.colors.pink : theme.colors.lightBlue}
    );

    ::placeholder {
      filter: drop-shadow(
        0rem 0rem 0.5rem
          ${({ theme, incorrect }) =>
            incorrect ? theme.colors.pink : theme.colors.lightBlue}
      );
    }
  }
`;

export default Input;
