import React from 'react';
import StandardButton from 'components/StandardButton';
import LoginFields from 'components/LoginFields';
import RegisterFields from 'components/RegisterFields';
import styled from 'styled-components';
import { Link } from 'gatsby';
import values from 'utils/values';
import validation from 'utils/validation';

const LoginContainer = styled.div`
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

class LoginComponent extends React.Component {
  constructor() {
    super();
    this.state = {
      login: '',
      password: '',
      retypedPassword: '',
      mode: '',
      incorrectLogin: false,
      incorrectPassword: false,
      incorrectRetypedPassword: false,
    };
  }

  handleLoginChange = event => {
    const login = event.target.value.substring(0, 50);
    this.setState({ login });
  };

  handlePasswordChange = event => {
    const password = event.target.value.substring(0, 50);
    this.setState({ password });
  };

  handleRetypedPasswordChange = event => {
    const retypedPassword = event.target.value.substring(0, 50);
    this.setState({ retypedPassword });
  };

  handleLoginButtonClick = () => {
    const { mode } = this.state;
    if (mode === 'login') {
      this.login();
    } else {
      this.resetState('login');
    }
  };

  handleRegisterButtonClick = () => {
    const { mode } = this.state;
    if (mode === 'register') {
      this.register();
    } else {
      this.resetState('register');
    }
  };

  login = () => {
    const { regularExpressions } = validation;
    const { login, password } = this.state;
    const incorrectLogin = !(
      regularExpressions.login.test(login) ||
      regularExpressions.email.test(login)
    );
    const incorrectPassword = !regularExpressions.password.test(password);
    this.setState({ incorrectLogin, incorrectPassword });
  };

  register = () => {
    const { regularExpressions } = validation;
    const { login, password, retypedPassword } = this.state;
    const incorrectLogin = !(
      regularExpressions.login.test(login) ||
      regularExpressions.email.test(login)
    );
    const incorrectPassword = !regularExpressions.password.test(password);
    const incorrectRetypedPassword = !(
      retypedPassword === password &&
      regularExpressions.password.test(retypedPassword)
    );
    this.setState({
      incorrectLogin,
      incorrectPassword,
      incorrectRetypedPassword,
    });
  };

  resetState = mode => {
    this.setState({
      login: '',
      password: '',
      retypedPassword: '',
      mode,
      incorrectLogin: false,
      incorrectPassword: false,
      incorrectRetypedPassword: false,
    });
  };

  render() {
    const {
      login,
      password,
      mode,
      retypedPassword,
      incorrectLogin,
      incorrectPassword,
      incorrectRetypedPassword,
    } = this.state;
    return (
      <LoginContainer>
        {mode === 'login' && (
          <LoginFields
            loginValue={login}
            passwordValue={password}
            incorrectLogin={incorrectLogin}
            incorrectPassword={incorrectPassword}
            onLoginChange={this.handleLoginChange}
            onPasswordChange={this.handlePasswordChange}
          />
        )}
        <StandardButton
          as={Link}
          onClick={this.handleLoginButtonClick}
          draggable="false"
        >
          {values.buttons.login}
        </StandardButton>
        {mode === 'register' && (
          <RegisterFields
            loginValue={login}
            passwordValue={password}
            retypedPasswordValue={retypedPassword}
            incorrectLogin={incorrectLogin}
            incorrectPassword={incorrectPassword}
            incorrectRetypedPassword={incorrectRetypedPassword}
            onLoginChange={this.handleLoginChange}
            onPasswordChange={this.handlePasswordChange}
            onRetypedPasswordChange={this.handleRetypedPasswordChange}
          />
        )}
        <StandardButton
          as={Link}
          draggable="false"
          onClick={this.handleRegisterButtonClick}
        >
          {values.buttons.register}
        </StandardButton>
      </LoginContainer>
    );
  }
}

export default LoginComponent;
