import styled from 'styled-components';
import React from 'react';
import logo from 'assets/images/myPocketLogo-assets/logox1_00x.png';
import { Link } from 'gatsby';

const ThemeHeaderContainer = styled.div`
  ${({ up }) => up && 'top: 0rem;'}
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
  cursor: pointer;
  transition: filter 0.3s;
  user-select: none;

  width: ${({ width }) => width || '80vmin'};

  :hover {
    filter: drop-shadow(
      0rem 0rem 0.5rem ${({ theme }) => theme.colors.lightBlue}
    );
  }
`;

const StyledImage = styled.img`
  object-fit: contain;
  height: auto;
  width: auto;
  max-width: 100%;
`;

const ThemeHeader = ({ up }) => (
  <ThemeHeaderContainer up={up} as={Link} draggable="false" to="/">
    <StyledImage src={logo} alt="logo" draggable="false" />
  </ThemeHeaderContainer>
);

export default ThemeHeader;
