import styled from 'styled-components';
import React from 'react';
import values from 'utils/values';
import validation from 'utils/validation';
import Input from 'components/Input';
import ValidationErrorMessage from 'components/ValidationErrorMessage';

const LoginFieldsContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

const LoginFields = ({
  loginValue,
  passwordValue,
  onLoginChange,
  onPasswordChange,
  incorrectLogin,
  incorrectPassword,
}) => {
  const messages = [];
  if (incorrectLogin) {
    messages.push(validation.validationErrors.login);
  }
  if (incorrectPassword) {
    messages.push(validation.validationErrors.password);
  }

  const validationMesages = messages.map((message, index) => (
    <ValidationErrorMessage
      key={'errormessage_'.concat(index)}
      message={message}
    />
  ));

  return (
    <LoginFieldsContainer>
      <Input
        type="text"
        autocomplete="off"
        autocorrect="off"
        autocapitalize="off"
        spellcheck="false"
        value={loginValue}
        onChange={onLoginChange}
        placeholder={values.placeholders.login}
        incorrect={incorrectLogin}
      />
      <Input
        type="password"
        autocomplete="off"
        autocorrect="off"
        autocapitalize="off"
        spellcheck="false"
        value={passwordValue}
        onChange={onPasswordChange}
        placeholder={values.placeholders.password}
        incorrect={incorrectPassword}
      />
      {validationMesages}
    </LoginFieldsContainer>
  );
};

export default LoginFields;
