const regularExpressions = {
  login: /^[a-zA-Z0-9.+_-]{5,50}$/,
  email: /^[a-zA-Z0-9.+_-]+@[a-zA-Z0-9.+_-]{5,50}$/,
  password: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/,
};

const validationErrors = {
  login: 'Login must contain at least 5 characters and cannot contain spaces.',
  password:
    'Password must contain at least 8 characters and at least one digit, lower case letter and upper case letter.',
  retypePassword: 'Retyped Password must be the same as origin password.',
};

const validation = {
  regularExpressions,
  validationErrors,
};

export default validation;
